/* Reset de márgenes y paddings */
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
  }
  
  /* Estilo del cuerpo de la página */
  body {
	font-family: 'Arial', sans-serif;
	background-color: #f2f2f2;
	color: #333;
  }
  
  /* Estilo del contenedor principal */
  .container {
	margin-top: 50px;
  }
  
  /* Estilo para el indicador de carga */
  .loading-spinner {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
  }
  
  /* Estilo para los títulos */
  h1 {
	text-align: center;
	margin-bottom: 20px;
  }
  
  /* Estilo para las imágenes */
  .img-fluid {
	max-width: 100%;
	height: auto;
  }
  
  /* Estilo para los párrafos */
  p {
	margin-bottom: 10px;
  }
  
  /* Estilo para los botones */
  .btn {
	display: inline-block;
	font-weight: 400;
	text-align: center;
	white-space: nowrap;
	vertical-align: middle;
	user-select: none;
	border: 1px solid transparent;
	padding: 0.375rem 0.75rem;
	font-size: 1rem;
	line-height: 1.5;
	border-radius: 0.25rem;
	transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  }
  
  .btn-primary {
	color: #fff;
	background-color: #007bff;
	border-color: #007bff;
  }
  
  .btn-primary:hover {
	color: #fff;
	background-color: #0069d9;
	border-color: #0062cc;
  }
  
  .btn-success {
	color: #fff;
	background-color: #28a745;
	border-color: #28a745;
  }
  
  .btn-success:hover {
	color: #fff;
	background-color: #218838;
	border-color: #1e7e34;
  }
  
  /* Estilo para los mensajes de alerta */
  .alert {
	padding: 0.75rem 1.25rem;
	margin-bottom: 1rem;
	border: 1px solid transparent;
	border-radius: 0.25rem;
  }
  
  .alert-warning {
	color: #856404;
	background-color: #fff3cd;
	border-color: #ffeeba;
  }
  
  .alert-danger {
	color: #721c24;
	background-color: #f8d7da;
	border-color: #f5c6cb;
  }
  
  /* Estilo para los botones en línea */
  .d-flex {
	display: flex;
  }
  
  /* Espaciado entre elementos en línea */
  .me-2 {
	margin-right: 0.5rem;
  }
  
  /* Estilo para el botón de regresar */
  .btn-success {
	margin-left: 10px;
  }

  /* Estilo del logo */
.logo {
	display: flex;
	align-items: center;
  }
  
  .logo-img {
	height: 50px; /* Ajusta el tamaño de la imagen */
	margin-right: 10px; /* Espacio entre la imagen y el texto */
  }
  .container {
	margin-top: 50px;
  }
  
  /* Estilo para los detalles del usuario */
  .user-details {
	background-color: #f9f9f9;
	padding: 20px;
	border-radius: 5px;
	border: 1px solid #ddd;
  }
  
  /* Estilo para la preformatted text */
  .user-details pre {
	white-space: pre-wrap;
	overflow: auto;
  }